.forgot {
    width: 100vw;
    height: 100vh;
    background-color: #f0f2f5;
    display: flex;
    align-items: center;
    justify-content: center;
}

.forgotWrapper {
    min-width: 20%;
    min-height: 20%;
    align-items: center;
    justify-content: center;
}

.forgotRight {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: #fff;
    -webkit-box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.68);
    box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.75),
    4px 4px 16px -8px rgba(0, 0, 0, 0.75);
}



.forgotBox {
    height: 250px;
    padding: 20px;
    background-color: white;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.forgotInput {
    height: 30px;
    border-radius: 10px;
    border: 1px solid gray;
    font-size: 18px;
    padding-left: 20px;
}

.forgotInput:focus {
    outline: none;
}

.forgotButton {
    height: 30px;
    border-radius: 10px;
    border: none;
    background-color: #1775ee;
    color: white;
    font-size: 20px;
    font-weight: 500;
    cursor: pointer;
}


