.sidebar {
    flex: 2;
    height: calc(100vh - 50px);
    overflow-y: scroll;
    /* position: sticky; */
    /* top: 50px; */
}

::-webkit-scrollbar {
    width: 5px;
}

::-webkit-scrollbar-track {
    background-color: #f1f1f1;
}

::-webkit-scrollbar-thumb {
    background-color: rgb(195, 182, 182);
}

.sidebarWrapper {
    padding: 20px;
}

.sidebarList {
    padding: 0;
    margin: 0;
    list-style: none;
}

.sidebarListItem {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}

.sidebarIcon {
    margin-right: 15px;
    cursor: pointer;
}

.sidebarButton {
    width: 150px;
    border: none;
    padding: 10px;
    border-radius: 5px;
    font-weight: 500;
}

.sidebarHr {
    margin: 20px 0;
}

.sidebarFriendList {
    margin: 0;
    padding: 0;
    list-style: none;
}


@media screen and (min-width: 1182px) {
    .sidebar {
        flex: 2;
        height: calc(100vh - 50px);
        overflow-y: scroll;
        position: sticky;
        top: 50px;
    }
}

@media screen and (max-width: 730px) {
    /* .sidebarList {
        display: grid;
        grid-template-rows: re
    } */

    .sidebarFriendList, .sidebarButton, .sidebarHr {
        display: none;
    }

    .sidebarWrapper {
        margin-bottom: 200px;
    }
}
